.o-form__group {
  display: flex;
  flex-direction: column;
  margin-bottom: 24px;
  &:last-child {
    margin-bottom: 0;
  }

  .o-footer__form-holder & {
    width: calc(50% - 16px);
      //  @media(max-width: 1000px) {
      //     max-width: 255px;
      // }
             @media(max-width: 768px) {
          width: auto;
      }
  }
}

.o-form__button-holder {
  display: flex;
  justify-content: center;
}
.o-form__button {
  @include button-main-medium;
    .o-footer__form-holder & {
    @include button-main-big;
        @media(max-width: 568px) {
          @include button-main-small;
      }
  }
}
  .o-form__label {
    .o-form__group_checked & {
    // text-align: left;
    // margin-left: 32px;
  }
}
 .o-form__group_checked {
  margin-bottom: 40px;
               @media(max-width: 768px) {
          margin: 40px 0;
      }
 }
.o-form__label {
  @include font-text-lato;
  cursor: pointer;
  padding-bottom: 12px;
  display: inline-flex;

    &:last-child {
    padding-bottom: 0;
  }
}
.o-form__input {
  padding: 14px;
      @media(max-width: 568px) {
          padding: 6px;
      }
}
input {
  &.o-form__input {
    border: 1px solid $main-color;
    @include font-text-lato; 
  }
}
// input:invalid{
//   &.o-form__input {
//     border: 1px solid $red;
//   }
// }

input:valid {
  &.o-form__input {
    border: 1px solid $main-color;
  }
}
input[type=tel] {
     &.o-form__input {
         
     }
 }
input[type=checkbox] {
     &.o-form__checkbox {
         display: none;
     }
 }

input[type="checkbox"]:checked.o-form__checkbox+label[for="confirmationInfo"] {
     &:before {
         content: '\2713';
         color: $main-color;
         font-size: 20px;
         line-height: 1;
         text-align: center;
     }

 }

 label[for="confirmationInfo"] {
     &.o-form__label {
       vertical-align: middle;
       @include  font-text-lato-small;
         &:before {
    content: '';
    position: relative;
    
    min-width: 20px;
    height: 20px;
    border: 1px solid $main-color;
    background-color: $white;
    vertical-align: middle;
    margin-right: 16px;
    // right: calc(34% - 4px);
         }
     }
 }
